*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
   
}
.footer__column{
 
    background-color: #ffffff;
    display: flex;
  
   
    
}
.products__title{
    margin-bottom: 40px;
    padding-top: 65px;
}
.footer__column-left{
    @include container();
    display: flex;
    flex-direction: row;
   
}
.footer__column-products{
    display: flex;
    flex-direction: column;
}
.product__items{
  display: flex;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
  color: #000000;
  font-family: './../../fonts/Montserrat/Montserrat-Regular.ttf';
}
.footer__column-customers{
   display: flex;
   flex-direction: column;
   margin-left: 110px;
  
}
.customers__title{
   padding-top: 65px;
   margin-bottom: 45px;
}
.customers__items{
    display: flex;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
  color: #000000;
  font-family: './../../fonts/Montserrat/Montserrat-Regular.ttf';
}
.footer__column-end {
    display: flex;
    flex-direction: column;
    margin-left: 144px;

}

.end__title{
    padding-top: 65px;
    margin-bottom: 22px;
}
.end__location{
    display: flex;
  margin-bottom: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
  color: #000000;
  font-family: './../../fonts/Montserrat/Montserrat-Regular.ttf';
}
.end__numbers{
  display: flex;
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
  color: #000000;
  font-family: './../../fonts/Montserrat/Montserrat-Regular.ttf';
}
.end__email{
    display: flex;
  margin-top: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 13px;
  color: #000000;
  font-family: './../../fonts/Montserrat/Montserrat-Regular.ttf';
}